export default {
  name: 'home',
  data() {
    return {
      title: 'home'
    }
  },
  methods: {
    tabClick(idx) {//tab切换更改路由
      switch (idx) {
        case 1:
          this.$goRoute('memberCen');
          break;
        case 2:
          this.$goRoute('shopInfo');
          break;
        default:
          this.$goRoute('homeMenu');
      }
    }
  }
}



