

import Vue from 'vue'
import App from './App'
import router from './router'


import VueResource from 'vue-resource'

Vue.use(VueResource);


import Vuex from 'vuex';

Vue.use(Vuex);


Vue.config.productionTip = true;


import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'


Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.component('tab', Tab);
Vue.component('tab-item', TabItem);
Vue.component('cell', Cell);
Vue.component('inline-x-number', InlineXNumber);
Vue.component('vSearch', Search);
Vue.component('x-input', XInput);
Vue.component('badge', Badge);
Vue.component('checker', Checker);
Vue.component('checker-item', CheckerItem);
Vue.component('check-list', Checklist);
Vue.component('popup-radio', PopupRadio);



Vue.prototype.$goRoute = function (index, params) {
this.$router.push({name: index, params: params});
};


const vuex_store = new Vuex.Store({
state: {
DDXTServerHost: 'http://127.0.0.1/Api/Terminal/',//接口基地址
picServerHost: 'http://127.0.0.1',//图片基地址

roomInfo: {},//房间信息初始化数据
GoodTypesData: {},//商品类型初始化数据
GoodsData: {},//商品初始化数据
WaiterData: {},//技师初始化数据
ChooseType: {},//上钟方式
userInfo: {},//会员信息数据
businessInfo: {},//店铺信息数据
orderInfo: {//订单数据信息
isOrder: false,//是否有订单
orderinfo: {},//主表信息
suborderinfo: []
}
},
getters: {//以下为vuex数据获取方法
getServerHost: state => {
return state.DDXTServerHost
},
getPicServerHost: state => {
return state.picServerHost
},



getRoomInfo: state => {
return state.roomInfo
},
getGoodTypes: state => {
return state.GoodTypesData
},
getGoods: state => {
return state.GoodsData
},
getWaiter: state => {
return state.WaiterData
},
getChooseType: state => {
return state.ChooseType
},
getUserInfo: state => {
return state.userInfo
},
getBusinessInfo: state => {
return state.businessInfo
},
getOrderInfo: state => {
return state.orderInfo
},
},
mutations: {//以下为vuex数据设置方法



setRoomInfo(state, val) {
state.roomInfo = val;
},
setGoodTypes(state, val) {
state.GoodTypesData = val;
},
setGoods(state, val) {
state.GoodsData = val;
},
setWaiter(state, val) {
state.WaiterData = val;
},
setChooseType(state, val) {
state.ChooseType = val;
},
setUserInfo(state, val) {
state.userInfo = val;
},
setBusinessInfo(state, val) {
state.businessInfo = val;
},
setOrderInfo(state, val) {
state.orderInfo = val;
}
}
});

/* eslint-disable no-new */
new Vue({
el: '#app',
store: vuex_store, //注入到vue
router,
render: h => h(App)
});





